"use client";
import SplitText from "@/components/motion/SplitText";

export default function PrivacyPolicyPage() {
  return (
    <main className="container-max py-16">
      <SplitText as="h1" className="font-heading text-4xl text-emerald" text="Privacy Policy" />
      <p className="mt-4 text-emerald/80">For QalbyMuslim. Last updated: October 23, 2025.</p>
      <p className="mt-6 text-emerald/80 max-w-3xl">This page summarizes how QalbyMuslim handles data such as location permissions, notifications, and anonymized diagnostics for improving services. For the detailed version previously on the static site, we can migrate the full content here on request.</p>
      <div className="mt-8">
        <a href="/privacy-policy.html" className="underline text-emerald">View original privacy policy</a>
      </div>
    </main>
  );
}
